import { Action } from '@ngrx/store';

export enum ActiveUserActionTypes {
  ActivingUser = '[ActiveUser] ActivingUser',
  ActivatedUser = '[ActiveUser] ActivatedUser',
}

export class ActivingUser implements Action {
  readonly type = ActiveUserActionTypes.ActivingUser;

  constructor(public payload: any) {}
}

export class ActivatedUser implements Action {
  readonly type = ActiveUserActionTypes.ActivatedUser;

  constructor(public payload: any) {}
}

export type ActionsActiveUser =
  | ActivingUser
  | ActivatedUser;



