import { Action } from '@ngrx/store';

export enum RecoveryPasswordActionTypes {
  SendEmail = '[RecoveryPassword] SendEmail',
  SendedEmail = '[RecoveryPassword] SendedEmail',
  UpdatingPassword = '[RecoveryPassword] UpdatingPassword',
  UpdatedPassword = '[RecoveryPassword] UpdatedPassword',
}

export class SendEmail implements Action {
  readonly type = RecoveryPasswordActionTypes.SendEmail;

  constructor(public payload: any) {}
}

export class SendedEmail implements Action {
  readonly type = RecoveryPasswordActionTypes.SendedEmail;

  constructor(public payload: any) {}
}

export class UpdatingPassword implements Action {
  readonly type = RecoveryPasswordActionTypes.UpdatingPassword;

  constructor(public payload: any) {}
}

export class UpdatedPassword implements Action {
  readonly type = RecoveryPasswordActionTypes.UpdatedPassword;

  constructor(public payload: any) {}
}

export type ActionsRecoveryPassword =
  | SendEmail
  | SendedEmail
  | UpdatingPassword
  | UpdatedPassword;



