import { NgModule } from '@angular/core';

import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';

import * as fromReducers from '@app/dashboard/modules/settings/store/reducers/settings.reducer';
import * as fromEffects from '@app/dashboard/modules/settings/store/effects/settings.effects';
import * as fromSettings from '@app/dashboard/modules/settings/services';

@NgModule({
  imports: [
    StoreModule.forFeature('settings', fromReducers.SettingsReducer),
    EffectsModule.forFeature([fromEffects.SettingsEffects]),
  ],
  providers: [
    ...fromSettings.services,
  ]
})
export class SettingsStoreModule { }



