import { NgModule } from '@angular/core';

import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';

import * as fromReducers from './reducers/active-user.reducer';
import * as fromEffects from './effects/active-user.effects';
import * as fromServices from '../services';

@NgModule({
  imports: [
    StoreModule.forFeature('active-user', fromReducers.ActiveUserReducer),
    EffectsModule.forFeature([fromEffects.ActiveUserEffects]),
  ],
  providers: [
    ...fromServices.services,
  ]
})
export class ActiveUserStoreModule { }



