import { createFeatureSelector, createSelector } from '@ngrx/store';

import * as fromStore from '../recovery-password.store';
import * as fromActions from '../actions/recovery-password.actions';

export function RecoveryPasswordReducer(
  state = fromStore.initialState,
  action: fromActions.ActionsRecoveryPassword): fromStore.RecoveryPasswordState {

  switch (action.type) {
    case fromActions.RecoveryPasswordActionTypes.SendEmail: {
      return { ...state, ...{
        isLoading: true
      }};
    }

    case fromActions.RecoveryPasswordActionTypes.SendedEmail: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    case fromActions.RecoveryPasswordActionTypes.UpdatingPassword: {
      return { ...state, ...{
        isLoading: true
      }};
    }

    case fromActions.RecoveryPasswordActionTypes.UpdatedPassword: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    default: {
      return state;
    }
  }
}

const exportLoading = (state: fromStore.RecoveryPasswordState) => state.isLoading;

export const selectRecoveryPasswordState = createFeatureSelector<fromStore.RecoveryPasswordState>('recovery-password');

export const getLoading = createSelector(selectRecoveryPasswordState, exportLoading);



