import { Injectable } from '@angular/core';

import { Action, Store } from '@ngrx/store';
import { Effect, Actions, ofType } from '@ngrx/effects';

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { tap, map, mergeMap, exhaustMap, catchError } from 'rxjs/operators';

import * as fromServicesShared from '@shared/services';
import * as fromServicesProfile from '@profile/services';

import * as fromStoreCoreActions from '@core/store/actions/router.actions';
import * as fromStoreShared from '@shared/store';

import * as fromServices from '../../services';
import * as fromStore from '../recovery-password.store';
import * as fromActions from '../actions/recovery-password.actions';

@Injectable()
export class RecoveryPasswordEffects {
  @Effect()
  sendingEmail$: Observable<Action> = this.actions$.pipe(
    ofType(fromActions.RecoveryPasswordActionTypes.SendEmail),
    map((action: fromActions.SendEmail) => action.payload),
    exhaustMap((email: any) =>
      this._service.recoveryPassword(email).pipe(
        map(response => new fromActions.SendedEmail(response)),
        catchError(error => of(new fromStoreShared.ErrorAlert(JSON.parse(error._body).message)))
      )
    )
  );

  @Effect({ dispatch: false })
  sendedEmail$ = this.actions$.pipe(
    ofType(fromActions.RecoveryPasswordActionTypes.SendedEmail),
    map((action: fromActions.SendedEmail) => action),
    tap((response) => {
      this._utils.showDialog({
        width: '350px',
        data: {
          title: 'Atención',
          message: response.payload.message
        },
      });
    })
  );

  @Effect()
  updatingPassword$: Observable<Action> = this.actions$.pipe(
    ofType(fromActions.RecoveryPasswordActionTypes.UpdatingPassword),
    map((action: fromActions.UpdatingPassword) => action.payload),
    exhaustMap((userToUpdate: any) =>
      this._serviceProfile.updatePassword(userToUpdate).pipe(
        map(response => new fromActions.UpdatedPassword(response)),
        catchError(error => of(new fromStoreShared.ErrorAlert(JSON.parse(error._body).message)))
      )
    )
  );

  @Effect({ dispatch: false })
  updatedPassowrd$ = this.actions$.pipe(
    ofType(fromActions.RecoveryPasswordActionTypes.UpdatedPassword),
    map((action: fromActions.UpdatedPassword) => action),
    tap((response) => {
      this._utils.showDialog({
        width: '350px',
        data: {
          title: 'Atención',
          message: response.payload.message
        },
        onClose: () => {
          this._store.dispatch(new fromStoreCoreActions.Go({
            path: ['/login'],
          }));
        }
      });
    })
  );

  constructor(
    private actions$: Actions,
    private _service: fromServices.RecoveryPasswordService,
    private _serviceProfile: fromServicesProfile.ProfileService,
    private _utils: fromServicesShared.UtilsService,
    private _store: Store<fromStore.RecoveryPasswordState>,
  ) { }
}



