import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { EllipsisPipe } from './ellipsis/ellipsis.pipe';

export const pipes: any[] = [
  EllipsisPipe
];

@NgModule({
    imports: [
      CommonModule
    ],
    declarations: pipes,
    exports: pipes
})
export class PipesModule { }



