import { Injectable } from '@angular/core';

import { Action, Store } from '@ngrx/store';
import { Effect, Actions, ofType } from '@ngrx/effects';

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { tap, map, mergeMap, exhaustMap, catchError } from 'rxjs/operators';

import * as fromServicesShared from '@shared/services';
import * as fromServicesProfile from '@profile/services';

import * as fromStoreCoreActions from '@core/store/actions/router.actions';
import * as fromStoreShared from '@shared/store';

import * as fromServices from '@app/presentation/modules/active-user/services';
import * as fromStore from '@app/presentation/modules/active-user/store/active-user.store';
import * as fromActions from '@app/presentation/modules/active-user/store/actions/active-user.actions';

@Injectable()
export class ActiveUserEffects {
  @Effect()
  activingUser$: Observable<Action> = this.actions$.pipe(
    ofType(fromActions.ActiveUserActionTypes.ActivingUser),
    map((action: fromActions.ActivingUser) => action.payload),
    exhaustMap((user: any) =>
      this._service.activeUser(user).pipe(
        map(response => new fromActions.ActivatedUser(response)),
        catchError(error => of(new fromStoreShared.ErrorAlert(JSON.parse(error._body).message)))
      )
    )
  );

  @Effect({ dispatch: false })
  activatedUser$ = this.actions$.pipe(
    ofType(fromActions.ActiveUserActionTypes.ActivatedUser),
    map((action: fromActions.ActivatedUser) => action),
    tap((response) => {
      this._utils.showDialog({
        width: '350px',
        data: {
          title: 'Atención',
          message: response.payload.message
        },
        onClose: () => {
          this._store.dispatch(new fromStoreCoreActions.Go({
            path: ['/login'],
          }));
        }
      });
    })
  );
  constructor(
    private actions$: Actions,
    private _service: fromServices.ActiveUserService,
    private _serviceProfile: fromServicesProfile.ProfileService,
    private _utils: fromServicesShared.UtilsService,
    private _store: Store<fromStore.ActiveUserState>,
  ) { }
}



