import { createFeatureSelector, createSelector } from '@ngrx/store';

import * as fromStore from '@app/presentation/modules/active-user/store/active-user.store';
import * as fromActions from '@app/presentation/modules/active-user/store/actions/active-user.actions';

export function ActiveUserReducer(
  state = fromStore.initialState,
  action: fromActions.ActionsActiveUser): fromStore.ActiveUserState {

  switch (action.type) {
    case fromActions.ActiveUserActionTypes.ActivingUser: {
      return { ...state, ...{
        isLoading: true
      }};
    }

    case fromActions.ActiveUserActionTypes.ActivatedUser: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    default: {
      return state;
    }
  }
}

const exportLoading = (state: fromStore.ActiveUserState) => state.isLoading;

export const selectActiveUserState = createFeatureSelector<fromStore.ActiveUserState>('active-user');

export const getLoading = createSelector(selectActiveUserState, exportLoading);



