import { createFeatureSelector, createSelector } from '@ngrx/store';

import * as fromStore from '@app/dashboard/modules/settings/store/settings.store';
import * as fromActions from '@app/dashboard/modules/settings/store/actions/settings.actions';

export function SettingsReducer(state = fromStore.initialState, action: fromActions.ActionsSettings): fromStore.SettingsState {
  switch (action.type) {
    case fromActions.SettingsActionTypes.GetUsers: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.SetUsers: {
      return { ...state, ...{
          isLoading: false,
          users: action.payload,
        }};
    }

    case fromActions.SettingsActionTypes.RegisteringUser: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.RegisteredUser: {
      return { ...state, ...{
          isLoading: false,
        }};
    }


    case fromActions.SettingsActionTypes.GetDevices: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.SetDevices: {
      return { ...state, ...{
          isLoading: false,
          devices: action.payload,
        }};
    }

    case fromActions.SettingsActionTypes.AddingDevice: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.AddedDevice: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    case fromActions.SettingsActionTypes.UpdatingDevice: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.UpdatedDevice: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    case fromActions.SettingsActionTypes.DeletingDevice: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.DeletedDevice: {
      return { ...state, ...{
          isLoading: false,
        }};
    }


    case fromActions.SettingsActionTypes.GetQuestions: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.SetQuestions: {
      return { ...state, ...{
          isLoading: false,
          questions: action.payload,
        }};
    }

    case fromActions.SettingsActionTypes.AddingQuestion: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.AddedQuestion: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    case fromActions.SettingsActionTypes.UpdatingQuestion: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.UpdatedQuestion: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    case fromActions.SettingsActionTypes.DeletingQuestion: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.DeletedQuestion: {
      return { ...state, ...{
          isLoading: false,
        }};
    }


    case fromActions.SettingsActionTypes.GetPayments: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.SetPayments: {
      return { ...state, ...{
          isLoading: false,
          payments: action.payload,
        }};
    }

    case fromActions.SettingsActionTypes.AddingPayment: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.AddedPayment: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    case fromActions.SettingsActionTypes.UpdatingPayment: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.UpdatedPayment: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    case fromActions.SettingsActionTypes.DeletingPayment: {
      return { ...state, ...{
          isLoading: true
        }};
    }

    case fromActions.SettingsActionTypes.DeletedPayment: {
      return { ...state, ...{
          isLoading: false,
        }};
    }

    default: {
      return state;
    }
  }
}

const exportLoading = (state: fromStore.SettingsState) => state.isLoading;
const exportUsers = (state: fromStore.SettingsState) => state.users;
const exportDevices = (state: fromStore.SettingsState) => state.devices;
const exportQuestions = (state: fromStore.SettingsState) => state.questions;
const exportPayments = (state: fromStore.SettingsState) => state.payments;

export const selectSettingsState = createFeatureSelector<fromStore.SettingsState>('settings');

export const getLoading = createSelector(selectSettingsState, exportLoading);
export const getUsers = createSelector(selectSettingsState, exportUsers);
export const getDevices = createSelector(selectSettingsState, exportDevices);
export const getQuestions = createSelector(selectSettingsState, exportQuestions);
export const getPayments = createSelector(selectSettingsState, exportPayments);



